// components/Header.js
"use client";

import { useState } from "react";
import Link from "next/link";
import Image from "next/image"; // ✅ import

export default function Header() {
  const [menuOpen, setMenuOpen] = useState(false); // mobile menu
  const [openDropdown, setOpenDropdown] = useState(null); // which mobile submenu is open

  const navLinks = [
    {
      name: "ABOUT JSS UNIVERSITY",
      href: "/",
      dropdown: [
        { name: "About JSS", href: "/" },
        { name: "Heritage", href: "/" },
      ],
    },
    {
      name: "ACADEMICS",
      href: "/",
      dropdown: [
        { name: "Schools", href: "/" },
        { name: "Departments", href: "/" },
        { name: "Programs", href: "/" },
      ],
    },
    {
      name: "ADMISSIONS",
      href: "/",
      dropdown: [
        { name: "Overview", href: "/" },
        { name: "Admission", href: "/" },
        { name: "UG Program", href: "/" },
      ],
    },
    {
      name: "FACILITIES",
      href: "/",
      dropdown: [
        { name: "Girls Hostel", href: "/" },
        { name: "Boys Hostel", href: "/" },
        { name: "Amenities Centre", href: "/" },
      ],
    },
    {
      name: "STUDENTS SUPPORT",
      href: "/",
      dropdown: [
        { name: "Student Life", href: "/" },
        { name: "Mentoring Scheme", href: "/" },
        { name: "Internal Complaint Committee", href: "/" },
      ],
    },
    {
      name: "EXAMINATION",
      href: "/",
      dropdown: [
        { name: "Code of Conduct", href: "/" },
        { name: "EMS- Portal link", href: "/" },
        { name: "Exam Regulations", href: "/" },
      ],
    },
  ];

  const toggleSubmenu = (idx) => {
    setOpenDropdown((prev) => (prev === idx ? null : idx));
  };

  return (
    <header className="site-header">
      <div className="nav-container">
        <Image
          src="/images/logo.png"
          alt="Site Logo"
          width={300}
          height={100}
          priority
        />
        <div className="right-header-section">
          <nav
            className={`navbar ${menuOpen ? "open" : ""}`}
            aria-label="Main navigation"
          >
            <ul className="nav-list header-item">
              {navLinks.map((link, idx) => (
                <li className="nav-item" key={idx}>
                  <div className="nav-link-row">
                    <Link href={link.href} className="nav-link">
                      {link.name}
                    </Link>

                    {link.dropdown && link.dropdown.length > 0 && (
                      // Toggle button only used for mobile; hidden on desktop via CSS
                      <button
                        className="submenu-toggle"
                        aria-expanded={openDropdown === idx}
                        aria-controls={`submenu-${idx}`}
                        onClick={(e) => {
                          e.stopPropagation();
                          toggleSubmenu(idx);
                        }}
                      ></button>
                    )}
                  </div>

                  {link.dropdown && link.dropdown.length > 0 && (
                    <ul
                      id={`submenu-${idx}`}
                      className={`dropdown-menu ${
                        openDropdown === idx ? "show-mobile" : ""
                      }`}
                      role="menu"
                      aria-label={`${link.name} submenu`}
                    >
                      {link.dropdown.map((sub, i) => (
                        <li key={i}>
                          <Link
                            href={sub.href}
                            className="dropdown-item"
                            role="menuitem"
                          >
                            {sub.name}
                          </Link>
                        </li>
                      ))}
                    </ul>
                  )}
                </li>
              ))}
            </ul>
          </nav>
          <button
            className="hamburger"
            onClick={() => {
              setMenuOpen((s) => !s);
              setOpenDropdown(null);
            }}
            aria-label="Toggle navigation"
            aria-expanded={menuOpen}
          >
            <svg
              width="22"
              height="16"
              viewBox="0 0 22 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <rect width="22" height="2" rx="1"></rect>
              <rect y="7" width="22" height="2" rx="1"></rect>
              <rect y="14" width="22" height="2" rx="1"></rect>
            </svg>
          </button>
        </div>
      </div>

      <style jsx>{`
        .right-header-section {
          display: flex;
          align-items: center;
        }
        .site-header {
          background: transparent;
          padding: 12px 20px;
          position: absolute;
          top: 0;
          z-index: 1;
          width: 100%;
        }
        .nav-container {
          max-width: 90%;
          margin: 0 auto;
          display: flex;
          align-items: center;
          justify-content: space-between;
        }
        .brand {
          font-weight: 700;
          font-size: 18px;
          text-decoration: none;
          color: inherit;
        }

        /* Hamburger */
        .hamburger {
          // display: none;
          background: none;
          border: none;
          padding: 6px;
          cursor: pointer;
        }
        .hamburger svg rect {
          fill: #fff;
        }
        .navbar {
          display: flex;
        }
        .nav-list {
          list-style: none;
          margin: 0;
          padding: 0;
          display: flex;
          gap: 28px;
          align-items: center;
          color: #fff;
          font-weight: 500;
        }
        .nav-item {
          position: relative;
        }
        .nav-link-row {
          display: flex;
          align-items: center;
          gap: 8px;
        }
        .nav-link {
          text-decoration: none;
          color: #111827;
          padding: 6px 8px;
          font-weight: 600;
        }

        .submenu-toggle {
          // display: none;
          background: none;
          border: none;
          cursor: pointer;
          font-size: 12px;
          padding: 6px;
        }

        .dropdown-menu {
          display: none;
          position: absolute;
          top: calc(100% + 6px);
          left: 0;
          min-width: 220px;
          background: #ffffff;
          border: 1px solid #e5e7eb;
          box-shadow: 0 6px 18px rgba(16, 24, 40, 0.08);
          border-radius: 6px;
          padding: 6px 0;
          z-index: 1000;
          list-style: none;
        }
        .nav-item:hover > .dropdown-menu,
        .nav-item:focus-within > .dropdown-menu {
          display: block;
        }
        .dropdown-item {
          display: block;
          padding: 8px 14px;
          text-decoration: none;
          color: #111827;
          font-size: 14px;
          white-space: nowrap;
        }
        .dropdown-item:hover,
        .dropdown-item:focus {
          background: #f3f4f6;
        }

        /* ===== MOBILE ===== */
        @media (max-width: 768px) {
          .hamburger {
            display: block;
          }
          .navbar {
            display: none;
            width: 100%;
          }
          .navbar.open {
            display: block;
          }
          .nav-list {
            flex-direction: column;
            align-items: stretch;
            gap: 0;
            margin-top: 12px;
          }
          .nav-item {
            border-top: 1px solid #eee;
          }
          .nav-link {
            display: block;
            padding: 12px 16px;
            width: 100%;
          }
          .submenu-toggle {
            display: inline-flex;
            align-items: center;
            justify-content: center;
            margin-right: 12px;
            color: #111827;
          }
          .nav-link-row {
            justify-content: space-between;
            padding: 0 12px;
          }

          /* Make dropdown flow inside document on mobile */
          .dropdown-menu {
            position: static;
            border: none;
            box-shadow: none;
            padding: 0;
            margin: 0;
            display: none;
          }
          .dropdown-menu.show-mobile {
            display: block;
          }
          .dropdown-item {
            padding: 10px 28px;
            border-top: 1px solid #f1f1f1;
          }
        }
      `}</style>
    </header>
  );
}
